.itemsizeContainer{
    display: flex;

}


.itemSize {
    width: 34px;
    height: 34px;
    margin:0 10px;
    background-color: #ececec;
    color: #3f3a3a;
    border-radius: 50%;
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}


.itemSize[aria-pressed=true]{
    background: #0d0d0d;
    border-width: 6px;
    color: #fff;
}

.detailPic{
    height: 30rem;
    float:right;
    margin:0 2rem;
}

.countBar{
    width: 5rem;
    height: 1rem;
    background: #3f3a3a;
}

.detailText{
    font-size: 1rem;
    letter-spacing: 4px;
    text-align: left;
    color: #3f3a3a;
    padding:0.5rem 0 0 0 ;
}


.detailColor{
    width: 1rem;
    height: 1rem;
    padding: 0.5rem 0rem;
    margin: 0 1rem;
    display:inline-block;
}


.cartBtn{
    width: 16rem;
    height: 2.5rem;
    padding: 0.4rem;
    margin:1rem 0;
    background-color: black;
    color:#fff;
}

.amountBtn{
    display: inline-flex;
    width: 8rem;
    height: 2rem;
    border: solid 1px gray;
    margin:1rem;
    padding:0.2rem 1rem;
    flex-direction: row;
    justify-content: space-between;
}


*{
    box-sizing: border-box;
    margin:0;
}


a{
    text-decoration: none;
    color:black;
}
